<template>
  <v-card elevation="2" outlined>
    <v-card-text>
      <p class='text-h6 text--primary'> Power Monitor </p>

      <v-container>
        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Time Selection</v-col>
          <v-col cols="8">
            <v-btn-toggle mandatory v-model="newTimeSelection">
              <div v-for="(item, index) in TimeSelection" :key="index">
                <v-btn outlined :value="item">
                  {{item}} Hour(s)
                </v-btn>
              </div>
            </v-btn-toggle>
          </v-col>
          <v-col cols="2">
            <v-btn-toggle v-model="enabledYAxis" multiple mandatory>
              <v-btn outlined > Power </v-btn>
              <v-btn outlined > Temperature </v-btn>
            </v-btn-toggle>
          </v-col>
        </v-row>

        <v-row>
          <v-col>
            <line-chart :chart-data="dataCollection"></line-chart>          
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" align-self="center">
            <v-btn class="ma-2"> Clear All </v-btn>
          </v-col>

          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Start</v-col>
          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
            <input type="datetime-local" class="text-body-1"/> 
          </v-col>

          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Stop</v-col>
          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
            <input type="datetime-local" class="text-body-1"/>
          </v-col>

          <v-col cols="2" align-self="center">
            <v-btn class="ma-2"> Export </v-btn>
          </v-col>
        </v-row>

      </v-container>

    </v-card-text>
    
    <v-card-text>
    
      <v-row>
        <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Unit On Time</v-col>
        <v-col cols="4" class='text-body-1 d-flex align-center font-weight-bold'> {{getTimeFromSeconds(OnTimeSeconds)}} </v-col>
        <v-col cols="6"></v-col>
      </v-row>
      <v-row>
        <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Transmit Time</v-col>
        <v-col cols="4" class='text-body-1 d-flex align-center font-weight-bold'> {{getTimeFromSeconds(TxTimeSeconds)}} </v-col>
        <v-col cols="6"></v-col>
      </v-row>

    </v-card-text>
  </v-card>
</template>


<script>
// Data
import graphData from "../../data/Entry1.json"

import LineChart from '../LineChart.vue'
import utils from '../../utils.js'

const TimeSelection = [1, 3, 6, 12, 24, 48];

export default {
  
  components: {
    LineChart
  },

  methods: {
    createGraphData(output, temperature, alcEnabled, alcAcitve, alcPower, xaxis) {
        var newLabels = [];
        var i = 0;
        if (xaxis == null) {
          if (output != null) {
            for (i = 0; i < output.length; i++) {
              newLabels.push(i + 1);
            }
          }
          else if (temperature != null) {
            for (i = 0; i < temperature.length; i++) {
              newLabels.push(i + 1);
            }
          }
        }
        else {
          newLabels = xaxis;
        }

        var newData = {};
        if (output != null && temperature != null) {
          newData = {
            labels: newLabels,
            datasets: [{
              borderColor: "#33818f",
              label: "Output Power (dBm)",
              borderWidth: 2,
              backgroundColor: "transparent",
              pointBorderColor: "transparent",
              data: output,
              yAxisID: "A"
            },
            {
              borderColor: "#d9a711",
              label: "Temperature (C)",
              borderWidth: 2,
              backgroundColor: "transparent",
              pointBorderColor: "transparent",
              data: temperature,
              yAxisID: "B"
            }]
          }
        }
        else if (output != null && temperature == null) {
          newData = {
            labels: newLabels,
            datasets: [{
              borderColor: "#33818f",
              label: "Output Power (dBm)",
              borderWidth: 2,
              backgroundColor: "transparent",
              pointBorderColor: "transparent",
              data: output,
              yAxisID: "A"
            }]
          }
        }
        else if (output == null && temperature != null) {
          newData = {
            labels: newLabels,
            datasets: [
            {
              borderColor: "#d9a711",
              label: "Temperature (C)",
              borderWidth: 2,
              backgroundColor: "transparent",
              pointBorderColor: "transparent",
              data: temperature,
              yAxisID: "B"
            }]
          }
        }

        if (alcEnabled && alcAcitve) {
          var calcaulatedAlcPower = alcPower;
          var AlcLine = [];
          for (i = 0; i < output.length; i++) {
            AlcLine.push(calcaulatedAlcPower);
          }

          newData.datasets.push({
            label: "ALC = " + (calcaulatedAlcPower).toFixed(1) + " dBm",
            borderWidth: 5,
            borderColor: "#671191",
            backgroundColor: "transparent",
            pointBorderColor: "transparent",
            data: AlcLine,
            borderDash: [50],
            yAxisID: "A",
          })
        }
        return newData;
    },

    createGraph(output, temperature, xaxis, conversion) {
      const powerLabel = utils.GetPowerUnit(conversion);
        
        var i = 0;

        var newData = {
          labels: xaxis,
          datasets: [],
        };

        if (output != null) {
          var calculatedPower = [];  
          for (i = 0; i < output.length; i++) {
            calculatedPower.push(utils.ConversionPowerUnit(output[i], conversion));
          }

          newData.datasets.push({
            borderColor: "#33818f",
            label: `Output Power (${powerLabel})`,
            borderWidth: 2,
            backgroundColor: "transparent",
            pointBorderColor: "transparent",
            data: calculatedPower,
            yAxisID: "A"
          });
        }

        if (temperature != null) {
          newData.datasets.push({
            borderColor: "#d9a711",
            label: "Temperature (C)",
            borderWidth: 2,
            backgroundColor: "transparent",
            pointBorderColor: "transparent",
            data: temperature,
            yAxisID: "B"
          });
        }

        return newData;
      },

    getTimeFromSeconds(seconds) {
      var hrs = (seconds / 3600).toFixed(0),
        mins = ((seconds % 3600) / 60).toFixed(0),
        secs = (seconds % 60).toFixed(0);
      return `${hrs} Hour(s) ${mins} Minute(s) ${secs} Second(s)`
    },

    generateGraph() {
      const max = this.TimeSelection[this.TimeSelection.length - 1];
      var startIndex = graphData.output.length - (graphData.output.length / (max / this.newTimeSelection));

      var output = this.enabledYAxis.includes(0) ? graphData.output.slice(startIndex) : null, 
        temperatures = this.enabledYAxis.includes(1) ? graphData.temperatures.slice(startIndex) : null,
        time = graphData.time.slice(startIndex);

      this.dataCollection = this.createGraph(output, temperatures, time, this.PowerConversion);

      // if (this.enabledYAxis.includes(0) && this.enabledYAxis.includes(1)) {
      //   this.dataCollection = this.createGraphData(
      //     output,
      //     temperatures,
      //     false, 
      //     false,
      //     0, 
      //     time);
      // }
      // else if (this.enabledYAxis.includes(0) && this.enabledYAxis.includes(1) == false) {
      //   this.dataCollection = this.createGraphData(
      //     output,
      //     null,
      //     false, 
      //     false,
      //     0, 
      //     time);
      // }
      // else {
      //   this.dataCollection = this.createGraphData(
      //     null,
      //     temperatures,
      //     false, 
      //     false,
      //     0, 
      //     time);
      // }
    },

  },

  props: {
    OnTimeSeconds: {
      type: Number,
      default: 36641
    },
    TxTimeSeconds: {
      type: Number,
      default: 18234
    },

    PowerConversion: {
      type: Number,
      default: 0
    }

  },

  data() {
    return {
      dataCollection: null,
      newTimeSelection: 12,

      enabledYAxis: [0, 1],
    }
  },

  watch: {
    newTimeSelection: function() {
      this.generateGraph();
    },

    enabledYAxis: function() {
      this.generateGraph();
    },

    // PowerConversion: function() {
    //   this.generateGraph();
    // }
  },

  created: function() {
    this.newTimeSelection = 6;
    this.TimeSelection = TimeSelection;
  }

}
</script>