<template>
  <v-card elevation="2" outlined>
    <v-card-text>
      <p class="text-h6 text--primary">
        Firmware Update
      </p>

      <v-container>
        <v-row align="center" :collapse="true">
          <v-col cols="4">
            <v-file-input label="Firmware File" accept="firmware/mm" browse-text="Select File" class="mx-1" 
              v-model="newFilename"></v-file-input>
          </v-col>
          <v-col cols="4">
            <v-btn class="mx-1" v-on:click="ConfirmFirmwareFile(newFilename)"> Upload </v-btn>
            <v-btn class="mx-1" > Cancel </v-btn>
          </v-col>
        </v-row>

        <v-row v-if="isValid">
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>New Firmware Version</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'>
            {{newFirmwareVersion}}
          </v-col>
        </v-row>

        <v-row v-if="isValid">
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Progress</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'>
            {{progress}}
          </v-col>

          <v-col cols="4">
            <v-progress-linear color="light-green darken-4" class="white--text font-weight-bold"
              :rotate="-90" :height="25" :value="percentage">
              {{percentage}}%
            </v-progress-linear>
          </v-col>
        </v-row>
      </v-container>

    </v-card-text>
  </v-card>

</template>


<script>
export default {
  name: 'UpdateFirmware',

  props: {

  },

  methods: {
    ConfirmFirmwareFile(fileName) {
      if (fileName != null && fileName.name.length > 0) {
        this.newFirmware = "20211012BD04";
        this.isValid = true;
        this.percentage = 0;
        this.progress = "";

        this.intervalPromise = setInterval(() => {
          this.percentage = this.percentage + 5;
          if (this.percentage == 100) {
            this.progress = "Firmware Update Completed";
            clearInterval(this.intervalPromise);
          }
          else if (this.percentage >= 50) {
            this.progress = "Reading Firmware";
          }
          else {
            this.progress = "Sending Firmware";
          }
        }, 250);

      }
    }
  },

  watch: {
    
  },

  data() {
    return {
      newFilename: null,
      newFirmwareVersion: "20211012BD04",
      isValid: false,
      percentage: 0,
      progress: null,

      intervalPromise: null
    };
  }
}
</script>