<template>
  <v-card elevation="2" outlined>
    <v-card-text>
      <p class='text-h6 text--primary'> Export/Import </p>

      <v-container>
        <v-btn class="mx-1" @click.stop="ExportDialog = true"> Export Settings </v-btn>
        <v-dialog v-model="ExportDialog" width="1000">
          <!-- <template v-slot:activator="{ on, attrs }">
            
          </template> -->
          <export-options></export-options>
        </v-dialog>


        <v-btn class="mx-1" v-on:click="importFunction()"> Import Settings </v-btn>
        <v-file-input id="configFileInput" v-model="newFilename" style="display: none;"> </v-file-input>
        <v-dialog v-model="ImportDialog" width="1000">
          <import-options></import-options>
        </v-dialog>

      </v-container>

    </v-card-text>
  </v-card>
</template>

<script>
import ExportOptions from './ExportOptions.vue'
import ImportOptions from './ImportOptions.vue'

export default {
  name: 'ImportExport',

  components: {
    ExportOptions,
    ImportOptions,
  },

  data() {
    return {
      ImportDialog: false,
      ExportDialog: false,
      newFilename: undefined,
      IsExport: false
    }
  },

  methods: {
    importFunction() {
      document.getElementById('configFileInput').click();
    }
  },

  watch: {
    newFilename() {
      if (this.newFilename != undefined) {
        this.ImportDialog = true;
      }
    },

    ImportDialog() {
      if (this.ImportDialog == false) {
        this.newFilename = undefined;
      }
    }
  }

}

</script>