<template>
  <div>
    <v-card elevation="2" outlined>
      <v-card-text>
        <p class="text-h6 text--primary">
          Fault Definitions
        </p>
        <v-container>
          <v-row>
            <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'> High RF Power </v-col>
            <v-col cols="3">
              <v-btn-toggle class="ma-2" mandatory color="primary" v-model="new_HighRfOption">
                <div v-for="(option, i) in FaultOptions" :key="i">
                  <v-btn outlined :value="i" :class="option.textColor" :color="option.background">
                    {{option.type}}
                  </v-btn>
                </div>
              </v-btn-toggle>
            </v-col>
            <v-col v-if="new_HighRfOption != 0" cols="3" align-self="center" class="text-body-1 font-weight-medium">
              <v-text-field :suffix="GetPowerUnit" dense hide-details
                v-model.number="new_HighRfPower"
                type="number" label="High RF Power"
                step="0.10"
                > </v-text-field>
            </v-col>
          </v-row>

          <v-row>
            <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'> Low RF Power </v-col>
            <v-col cols="3">
              <v-btn-toggle class="ma-2" mandatory color="primary" v-model="new_LowRfOption">
                <div v-for="(option, i) in FaultOptions" :key="i">
                  <v-btn outlined :value="i" :class="option.textColor" :color="option.background">
                    {{option.type}}
                  </v-btn>
                </div>
              </v-btn-toggle>
            </v-col>
            <v-col v-if="new_LowRfOption != 0" cols="3" align-self="center" class="text-body-1 font-weight-medium">
              <v-text-field :suffix="GetPowerUnit" dense hide-details
                v-model.number="new_LowRfPower"
                type="number" label="Low RF Power"
                step="0.10"
                > </v-text-field>
            </v-col>
          </v-row>

          <v-row>
            <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'> High Reflected Power </v-col>
            <v-col cols="3">
              <v-btn-toggle class="ma-2" mandatory color="primary" v-model="new_HighReflectedOption">
                <div v-for="(option, i) in FaultOptions" :key="i">
                  <v-btn outlined :value="i" :class="option.textColor" :color="option.background">
                    {{option.type}}
                  </v-btn>
                </div>
              </v-btn-toggle>
            </v-col>
            <v-col v-if="new_HighReflectedOption != 0" cols="3" align-self="center" class="text-body-1 font-weight-medium">
              <v-text-field :suffix="GetPowerUnit" dense hide-details
                v-model.number="new_HighReflectedPower"
                type="number" label="High Reflected Power"
                step="0.10"
                > </v-text-field>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
    </v-card>

    <v-card elevation="2" outlined>
      <v-card-text>
        <p class="text-h6 text--primary">
          Inhibit

          <v-btn-toggle mandatory color="primary" class="ma-2">
            <v-btn outlined class="primary--text">
              Closed
            </v-btn>
            <v-btn outlined disabled class="primary--text">
              Open
            </v-btn>
          </v-btn-toggle>
        </p>
        
      </v-card-text>
    </v-card>

  </div>  
</template>


<script>

import utils from '../../utils.js'

const FaultOptions = [
  {type: "Off", background: "", textColor: ""},
  {type: "Alarm", background: "amber", textColor: "amber--text"},
  {type: "Fault", background: "red", textColor: "red--text"}];

export default {
  name: 'RfSettings',
  
  props: {
    FaultSettings: {
      type: Object,
      default: function() {
        return {
          HighRfPower:41.2,
          HighRfOption: 2,

          LowRfPower: 21.5,
          LowRfOption: 2,

          ReflectedPower: 35.6,
          ReflectedOption: 2
        };
      }
    },
    
    PowerConversion: {
      type: Number,
      default: 0
    }
  },

  created() {
    this.FaultOptions = FaultOptions;
  },

  computed: {
    GetPowerUnit() {
      return utils.GetPowerUnit(this.PowerConversion);
    },

  },

  methods: {
  },

  watch: {
    PowerConversion: function() {
      this.new_HighRfPower = utils.ConversionPowerUnit(this.FaultSettings.HighRfPower, this.PowerConversion);
      this.new_LowRfPower = utils.ConversionPowerUnit(this.FaultSettings.LowRfPower, this.PowerConversion);
      this.new_HighReflectedPower = utils.ConversionPowerUnit(this.FaultSettings.ReflectedPower, this.PowerConversion);
    }
  },

  data() {
    return {
      new_HighRfOption: this.FaultSettings.HighRfOption,
      new_HighRfPower : utils.ConversionPowerUnit(this.FaultSettings.HighRfPower, this.PowerConversion),

      new_LowRfOption: this.FaultSettings.LowRfOption,
      new_LowRfPower : utils.ConversionPowerUnit(this.FaultSettings.LowRfPower, this.PowerConversion),

      new_HighReflectedOption: this.FaultSettings.ReflectedOption,
      new_HighReflectedPower : utils.ConversionPowerUnit(this.FaultSettings.ReflectedPower, this.PowerConversion),
    }
  }


}
</script>

<style scoped>
.input-field {
  padding: 5px;
  margin: 5px;
}
</style>