<template>
  <v-card elevation="2" outlined>
    <v-row>
      <v-col>
        <v-data-table :headers="headers" :items="events" :items-per-page="10" class="text-body-1">
          <template v-slot:item.DateTime="{ item }">
            <span> {{formatDateTime(item.DateTime)}} </span>
          </template>

          <template v-slot:item.EventType="{ item }">
            <span :class="[item.EventType == 'Alarm' ? 'red--text' : (item.EventType == 'Warning' ? 'amber--text' : '')]"> {{item.EventType}} </span>
          </template>
        </v-data-table>
      </v-col>
    </v-row>

    <v-row>
      <v-col cols="2" align-self="center">
        <v-btn v-on:click="ClearAll">
          Clear All 
        </v-btn>
      </v-col>

      <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Start</v-col>
      <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
        <input type="datetime-local" class="text-body-1"/> 
      </v-col>

      <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Stop</v-col>
      <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
        <input type="datetime-local" class="text-body-1"/>
      </v-col>

      <v-col cols="2" align-self="center">
        <v-btn class="ma-2"> Export </v-btn>
      </v-col>
    </v-row>
        
  </v-card>
</template>

<script>

import eventData from '../../data/Events.json'

export default {
  name: 'MissionEvent',

  methods: {
    formatDateTime(time) {
      var currentTime = new Date(time);
      return currentTime.toISOString().split('.')[0];
    },

    ClearAll() {
      this.events = [];
    }
  },

  data () {
    return {
      headers: [
        {
          text: "Date",
          value: "DateTime",
          class: "grey darken-1 white--text text-body-1"
        },
        {
          text: "Event Type",
          value: "EventType",
          class: "grey darken-1 white--text text-body-1"
        },
        {
          text: "Description",
          value: "EventDescription",
          class: "grey darken-1 white--text text-body-1"
        }
      ],
      events: eventData.Events
    }
  },
}
</script>