<!-- Chart Vue JS does not need template since it's being inject via javascript -->

<!---------------------------------------------------------------------------->

<!---------------------------------------------------------------------------->
<script>

import { Line, mixins } from 'vue-chartjs';

const { reactiveProp } = mixins;

export default {
  extends: Line,
  mixins: [reactiveProp],
  
  data: function() {
    return {
      options: {
        scales: {
          yAxes: 
          [{
            id: "A",
            scaleLabel: {
              display: true,
              labelString: "dBm"
            },
            position: "left",
          },
          {
            id: "B",
            scaleLabel: {
              display: true,
              labelString: "C"
            },
            position: "right",
          }],
          xAxes: [{
            display: true,
            type: 'time',
          }]
        },
        legend: {
          display: true
        },
        maintainAspectRatio: false
      }
    }
  },

}
</script>

